import { DelTableServicesProvider } from "../../providers/del-table-services/del-table-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { DelTableViewModel } from "../../Model/ViewModel/DelTableViewModel";

/**
 * Generated class for the DelTableModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-Del-table-add-edit-modal",
  templateUrl: "Del-table-add-edit-modal.html"
})
export class DelTableAddEditModalPage {
  item: DelTableViewModel;

  mode: string;
  CanEditBatch: boolean;
  title: string;
  DelTable_type: string;
  min_exec_table_seq: number = 100000;
  max_exec_table_seq: number = this.min_exec_table_seq + 9999;
  range_exec_table_seq: string;
  change_mode: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private DelTableServices: DelTableServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController
  ) {
    this.range_exec_table_seq = String.Format(
      "{0}-{1}",
      this.min_exec_table_seq,
      this.max_exec_table_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new DelTableViewModel();
    this.item.exec_table_seq = navParams.data.item.exec_table_seq;
    this.item.exec_group = navParams.data.item.exec_group;
    this.item.tablefullname = navParams.data.item.tablefullname;
    this.item.date_columnname = navParams.data.item.date_columnname;
    this.item.dateformat_columnname = navParams.data.item.dateformat_columnname;
    this.item.datepart = navParams.data.item.datepart;
    this.item.keep_num = navParams.data.item.keep_num;
    this.item.del_method_key = navParams.data.item.del_method_key;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.is_active = navParams.data.item.is_active;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.del_method = navParams.data.item.del_method;

    if (this.change_mode === "c") {
      this.get_max_exec_table_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title = "刪除資料表";
  }

  SelectImportMode() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.del_method_key,
      code_type: "027"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.del_method_key = select_data.code_key;
      this.item.del_method = select_data.code_desc;
    });

    modal.present();
  }

  SelectTable() {
    let modal = this.modalCtrl.create("TableExtSelectModalPage", {
      select_item: this.item.tablefullname
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;

      this.item.tablefullname = select_data.tablefullname;
    });

    modal.present();
  }

  get_max_exec_table_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.DelTableServices.GetMaxExecTableSeqAsync(
        this.item.exec_group,
        this.min_exec_table_seq,
        this.max_exec_table_seq
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_table_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.DelTableServices.GetMaxExecSeqAsync(this.item.exec_group).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad DelTableModalPage");
  }
}



