export class DelTableViewModel {
/// <summary>
///  轉資料表序號
/// <summary>
public exec_table_seq: number;
/// <summary>
///  轉檔群組
/// <summary>
public exec_group: string;
/// <summary>
///  資料表全名
/// <summary>
public tablefullname: string;
/// <summary>
///  選單日期資料表全名
/// <summary>
public combo_tablefullname: string;
/// <summary>
///  資料日期欄位名稱
/// <summary>
public date_columnname: string;
/// <summary>
///  資料日期格式欄位名稱
/// <summary>
public dateformat_columnname: string;
/// <summary>
///  日期部位
/// <summary>
public datepart: string;
/// <summary>
///  保留日期部位數字
/// <summary>
public keep_num: number;
/// <summary>
///  刪除資料方法主鍵
/// <summary>
public del_method_key: string;
/// <summary>
///  執行順序
/// <summary>
public exec_seq: number;
/// <summary>
///  啟用註記
/// <summary>
public is_active: boolean;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;

/// <summary>
///  啟用說明
/// <summary>
public is_active_desc: string;
/// <summary>
///  啟用顏色
/// <summary>
public is_active_color: string;
/// <summary>
///  啟用顏色
/// <summary>
public is_active_color_right: string;
/// <summary>
///  刪除資料方法
/// <summary>
public del_method: string;

/// <summary>
///  日期部位
// <ion-option value="yyyy">年</ion-option>
// <ion-option value="qq">季</ion-option>
// <ion-option value="mm">月</ion-option>
// <ion-option value="d">日</ion-option>
// <ion-option value="wk">週(1-52)</ion-option>
// <ion-option value="dw">星期(1-7)</ion-option>
// <ion-option value="hh">小時(0-23)</ion-option>
// <ion-option value="n">分</ion-option>
// <ion-option value="ss">秒</ion-option>
/// <summary>
public datepart_desc: string;


}



