import {StringResponse} from '../../Model/Response/StringResponse';
import {NumberResponse} from '../../Model/Response/NumberResponse';
import {String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { DelTableListResponse } from "../../Model/Response/DelTableListResponse";
import { DelTableResponse } from "../../Model/Response/DelTableResponse";
import { DelTableViewModel } from '../../Model/ViewModel/DelTableViewModel';


/*
  Generated class for the DelTableServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class DelTableServicesProvider {
  private DelTables: DelTableListResponse;
  private DelTable: DelTableResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "exec_table_seq";
    console.log("Hello ServicesProvider Provider");
  }




  public GetMaxExecTableSeqAsync(exec_group:string,min_exec_table_seq:number,max_exec_table_seq:number) {
    var sub_url: string;
    sub_url = "DelTable/get_max_exec_table_seq?exec_group={1}&min_exec_table_seq={2}&max_exec_table_seq={3}";
    sub_url = String.Format(sub_url, this.ctl,exec_group,min_exec_table_seq,max_exec_table_seq);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public GetMaxExecSeqAsync(exec_group:string) {
    var sub_url: string;
    sub_url = "DelTable/get_max_exec_seq?exec_group={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(exec_table_seq:string,method:string) {
    var sub_url: string;
    sub_url = "DelTable/get_{2}_sql?exec_table_seq={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public GetListsByAsync(exec_group:string,PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "DelTable?PageSize={1}&PageNumber={2}&keyword={3}&pexec_group={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.DelTables = new DelTableListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.DelTables;
    });
  }



  public PostPutAsync(DelTableViewModel:DelTableViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "DelTable";
    else
    sub_url = "DelTable/{1}";
    sub_url = String.Format(sub_url, this.ctl,DelTableViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,DelTableViewModel,mode).map(
      item => {
        this.DelTable = new DelTableResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.DelTable;
      }
    );
  }
  public GetDelTableSqlAsync(tablefullname:string, date_columnname:string, dateformat_columnname:string, datepart:string,keep_num:number, del_method_key:string) {
    var sub_url: string;
    sub_url = "DelTable/gen_del_table_sql?tablefullname={1}&date_columnname={2}&dateformat_columnname={3}&datepart={4}&keep_num={5}&del_method_key={6}";
    sub_url = String.Format(sub_url, this.ctl,tablefullname,  date_columnname, dateformat_columnname, datepart, keep_num, del_method_key);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public DeleteAsync(exec_table_seq:number) {
    var sub_url: string;
    sub_url = "DelTable/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }
}



