import { DelTableViewModel } from "../ViewModel/DelTableViewModel";
import { ListModelResponse } from "./ListModelResponse";
import { String } from "../../Model/String";
export class DelTableListResponse extends ListModelResponse<DelTableViewModel> {
  Model: DelTableViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Model = [];
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      obj.Model.forEach(data => {
        let item = new DelTableViewModel();
        item.exec_table_seq = data.exec_table_seq;
        item.exec_group = data.exec_group;
        item.tablefullname = data.tablefullname;
        item.combo_tablefullname = (data && data.combo_tablefullname) || null;
        item.date_columnname = data.date_columnname;
        item.dateformat_columnname = data.dateformat_columnname;
        item.datepart = data.datepart;
        item.keep_num = data.keep_num;
        item.del_method_key = data.del_method_key;
        item.exec_seq = data.exec_seq;
        item.is_active = data.is_active;
        item.creator = data.creator;
        item.create_time = data.create_time;
        item.modifier = data.modifier;
        item.last_update_time = data.last_update_time;

        item.is_active_desc = data.is_active_desc;
        switch (data.is_active) {
          case false: {
            item.is_active_color = "danger";
            item.is_active_color_right = "danger-right";
            break;
          }

          default: {
            item.is_active_color = "active";
            item.is_active_color_right = "active-right";
          }
        }
        // <ion-option value="yyyy">年</ion-option>
        // <ion-option value="qq">季</ion-option>
        // <ion-option value="mm">月</ion-option>
        // <ion-option value="d">日</ion-option>
        // <ion-option value="wk">週(1-52)</ion-option>
        // <ion-option value="dw">星期(1-7)</ion-option>
        // <ion-option value="hh">小時(0-23)</ion-option>
        // <ion-option value="n">分</ion-option>
        // <ion-option value="ss">秒</ion-option>
        switch (data.datepart) {
          case "yyyy":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "年");
            break;
          case "qq":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "季");
            break;
          case "mm":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "月");
            break;
          case "d":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "日");
            break;
          case "wk":
            item.datepart_desc = String.Format(
              "{0}{1}",
              data.keep_num,
              "週(1-52)"
            );
            break;
          case "dw":
            item.datepart_desc = String.Format(
              "{0}{1}",
              data.keep_num,
              "星期(1-7)"
            );
            break;
          case "hh":
            data.datepart_desc = String.Format(
              "{0}{1}",
              data.keep_num,
              "小時(0-23)"
            );
            break;
          case "n":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "分");
            break;
          case "ss":
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "秒");
            break;
          default:
            item.datepart_desc = String.Format("{0}{1}", data.keep_num, "月");
        }

        item.del_method = data.del_method;
        this.Model.push(item);
      }) || null;
    }
  }
}



