import { SingleModelResponse } from "./SingleModelResponse";
import { DelTableViewModel } from "../ViewModel/DelTableViewModel";

export class DelTableResponse extends SingleModelResponse<DelTableViewModel> {
  Model: DelTableViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new DelTableViewModel();
      this.Model.exec_table_seq=obj.Model.exec_table_seq;
this.Model.exec_group=obj.Model.exec_group;
this.Model.tablefullname=obj.Model.tablefullname;
this.Model.combo_tablefullname=obj && obj.Model.combo_tablefullname || null;
this.Model.date_columnname=obj.Model.date_columnname;
this.Model.dateformat_columnname=obj.Model.dateformat_columnname;
this.Model.datepart=obj.Model.datepart;
this.Model.keep_num=obj.Model.keep_num;
this.Model.del_method_key=obj.Model.del_method_key;
this.Model.exec_seq=obj.Model.exec_seq;
this.Model.is_active=obj.Model.is_active;
this.Model.creator=obj.Model.creator;
this.Model.create_time=obj.Model.create_time;
this.Model.modifier=obj.Model.modifier;
this.Model.last_update_time=obj.Model.last_update_time;
this.Model.del_method=obj.Model.del_method;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
        this.Model.is_active_color = "active";
      }
    }
  }
}



